
.todos-main {
    background: $bg-color;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .2);
    position: relative;

    &:before,
    &:after {
        content: '';
        position: absolute;
        top: 100%;
        background: $bg-color;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .2);
        z-index: -1;
        }
    &:before {
        height: 10px;
        left: 4px;
        right: 4px;
        }
    &:after {
        height: 5px;
        left: 2px;
        right: 2px;
        }
    }